.container {
    padding: 0 20px;
  }
  
  @media (min-width: 768px) {
    .container {
      padding: 0 30px;
    }
  }
  
  @media (min-width: 1000px) {
    .container {
      padding: 0 40px;
    }
  }
  
  @media (min-width: 1600px) {
    .container {
      margin: 0 auto;
      max-width: 1600px;
    }
  }